import React, { useEffect, useState } from 'react';
import { withRouter, useHistory } from 'react-router-dom';
import { Card, Spin } from 'antd';
import Axios from 'axios';
import classes from './Adds.module.scss';
import AddDetail from '../../add-detail/AddDetail';
import options from '../../../../app-data/options';
import { subCats } from '../../../../app-data/subcats';
import P from '../../../re-use/p/P';

// Redux
import { connect } from 'react-redux';
import {
	addLocation,
	addSubcategory,
	fetchData,
	setEnd,
	setStart,
	setUrl,
	setSpin,
	setStartEnd,
	setLen
} from '../../../../redux/action';
import InfiniteScroll from 'react-infinite-scroll-component';
import { AxiosFunc } from '../../axios/AxiosFunc';

function Adds(props) {
	const url = useHistory().location;
	const [ urlChanged, setUrlChanged ] = useState(false);
	const [ value, setValue ] = useState(0.8);

	const fetchOnScroll = () => {
		Axios.post(`https://ravid-app.herokuapp.com/${props.axiosUrl}`, props.axiosObj)
			.then((res) => {
				const sliced = res.data.slice(props.start, props.end);
				const concated = props.fetchedData.concat(sliced);
				props.fetchData(concated);
				props.setStart();
				props.setEnd();
			})
			.catch((e) => {
				console.log('err');
			});
	};

	useEffect(
		() => {
			props.setStartEnd();
			setUrlChanged((prev) => !prev);
		},
		[ props.match ]
	);

	useEffect(
		() => {
			const { location, search, subcategory } = props.match.params;

			switch (url.pathname) {
				case '/cars':
					props.addSubcategory('');
					if (props.appLocation) {
						const option = options.find((any) => any.value === props.appLocation);
						props.history.push(`/cars/${option.url}`);
					}
					if (url.search) {
						AxiosFunc(`getByCategory${url.search}`, {
							category: 'cars'
						});
					} else {
						AxiosFunc(`getByCategory`, {
							category: 'cars'
						});
					}
					return undefined;
				case '/estate':
					props.addSubcategory('');
					if (props.appLocation) {
						const option = options.find((any) => any.value === props.appLocation);
						props.history.push(`/estate/${option.url}`);
					}
					if (url.search) {
						AxiosFunc(`getByCategory${url.search}`, {
							category: 'estate'
						});
					} else {
						AxiosFunc(`getByCategory`, {
							category: 'estate'
						});
					}
					return undefined;
				case '/phones':
					props.addSubcategory('');
					if (props.appLocation) {
						const option = options.find((any) => any.value === props.appLocation);
						props.history.push(`/phones/${option.url}`);
					}
					if (url.search) {
						AxiosFunc(`getByCategory${url.search}`, {
							category: 'phones'
						});
					} else {
						AxiosFunc(`getByCategory`, {
							category: 'phones'
						});
					}
					return undefined;
				case '/other':
					props.addSubcategory('');
					if (props.appLocation) {
						const option = options.find((any) => any.value === props.appLocation);
						props.history.push(`/other/${option.url}`);
					}
					if (url.search) {
						AxiosFunc(`getByCategory${url.search}`, {
							category: 'other'
						});
					} else {
						AxiosFunc(`getByCategory`, {
							category: 'other'
						});
					}
					return undefined;
				case '/account':
					props.setSpin(true);
					Axios.post(
						'https://ravid-app.herokuapp.com/getUserAds',
						{
							id: localStorage.getItem('id')
						},
						{ headers: { Authorization: `bearer ${localStorage.getItem('token')}` } }
					)
						.then((res) => {
							props.setLen(res.data.length);
							props.fetchData(res.data.ads);
							props.setSpin(false);
						})
						.catch((e) => {
							console.log(e.message + 'Axios err bro');
						});

					return undefined;
			}

			switch (props.match.path) {
				case '/cars/:location':
					props.addSubcategory('');
					const loc = options.find((any) => any.url === props.match.params.location);
					if (!loc) {
						return props.history.push('/');
					}
					props.addLocation(`${loc.value}`);
					if (url.search) {
						AxiosFunc(`getByCatAndLoc${url.search}`, {
							category: 'cars',
							location: location
						});
					} else {
						AxiosFunc(`getByCatAndLoc`, {
							category: 'cars',
							location: location
						});
					}
					return console.log('yo');
				case '/estate/:location':
					props.addSubcategory('');
					const loc2 = options.find((any) => any.url === props.match.params.location);
					if (!loc2) {
						return props.history.push('/');
					}
					props.addLocation(`${loc2.value}`);

					if (url.search) {
						AxiosFunc(`getByCatAndLoc${url.search}`, {
							category: 'estate',
							location: location
						});
					} else {
						AxiosFunc(`getByCatAndLoc`, {
							category: 'estate',
							location: location
						});
					}
					return undefined;
				case '/phones/:location':
					props.addSubcategory('');
					const loc3 = options.find((any) => any.url === props.match.params.location);
					if (!loc3) {
						return props.history.push('/');
					}
					props.addLocation(`${loc3.value}`);

					if (url.search) {
						AxiosFunc(`getByCatAndLoc${url.search}`, {
							category: 'phones',
							location: location
						});
					} else {
						AxiosFunc(`getByCatAndLoc `, {
							category: 'phones',
							location: location
						});
					}
					return undefined;
				case '/other/:location':
					props.addSubcategory('');
					const loc4 = options.find((any) => any.url === props.match.params.location);
					if (!loc4) {
						return props.history.push('/');
					}
					props.addLocation(`${loc4.value}`);

					if (url.search) {
						AxiosFunc(`getByCatAndLoc${url.search}`, {
							category: 'other',
							location: location
						});
					} else {
						AxiosFunc(`getByCatAndLoc`, {
							category: 'other',
							location: location
						});
					}
					return undefined;
			}

			if (search && location && subcategory) {
				const subcat = subCats.find((any) => any.url === subcategory);
				if (!subcat) {
					props.history.push('/');
					return undefined;
				}
				const loc = options.find((any) => any.url === location);
				if (!loc) {
					props.history.push('/');
					return undefined;
				}
				if (!props.appLocation && !props.subcategory) {
					const loc = options.find((any) => any.url === location);
					props.addLocation(loc.value);
					props.addSubcategory(subcategory);
					console.log('added sub and loc');
				}
				if (!props.appLocation) {
					const loc = options.find((any) => any.url === location);
					props.addLocation(loc.value);
					console.log('added  loc');
				}
				if (!props.subcategory) {
					props.addSubcategory(subcategory);
					console.log('added sub');
				}
				if (url.search) {
					AxiosFunc(`getBySerAndLocAndSub${url.search}`, {
						location: location,
						search: search,
						subcategory: subcategory
					});
				} else {
					AxiosFunc(`getBySerAndLocAndSub`, {
						location: location,
						search: search,
						subcategory: subcategory
					});
				}

				return undefined;
			}
			if (search && subcategory) {
				const subcat = subCats.find((any) => any.url === subcategory);
				if (!subcat) {
					props.history.push('/');
					return undefined;
				}
				if (!props.subcategory) {
					props.addSubcategory(subcategory);
				}
				if (url.search) {
					AxiosFunc(`getBySerAndSub${url.search}`, {
						search: search,
						subcategory: subcategory
					});
				} else {
					AxiosFunc(`getBySerAndSub`, {
						search: search,
						subcategory: subcategory
					});
				}
				return console.log('search with subcategory ');
			}
			if (search && location) {
				const loc = options.find((any) => any.url === location);
				if (!loc) {
					props.history.push('/');
					return console.log('pushd /');
				}
				if (!props.appLocation) {
					const loc = options.find((any) => any.url === location);
					props.addLocation(loc.value);
					console.log('added  loc');
				}
				if (url.search) {
					AxiosFunc(`getBySerAndLoc${url.search}`, {
						location: location,
						search: search
					});
				} else {
					AxiosFunc(`getBySerAndLoc`, {
						location: location,
						search: search
					});
				}
				return console.log('search with loc ');
			}
			if (location && subcategory) {
				const subcat = subCats.find((any) => any.url === subcategory);
				if (!subcat) {
					props.history.push('/');
					return undefined;
				}
				const loc = options.find((any) => any.url === location);
				if (!loc) {
					props.history.push('/');
					return console.log('pushd /');
				}
				if (!props.appLocation) {
					const item = options.find((any) => any.url === location);
					props.addLocation(item.value);
					console.log('added  loc');
				}
				if (!props.subcategory) {
					props.addSubcategory(subcategory);
					console.log('added sub ');
				}
				if (url.search) {
					AxiosFunc(`getBySubAndLoc${url.search}`, { subcategory: subcategory, location: location });
				} else {
					AxiosFunc(`getBySubAndLoc`, { subcategory: subcategory, location: location });
				}
				return console.log('loc sub ');
			}
			if (search) {
				if (url.search) {
					AxiosFunc(`getBySearch${url.search}`, {
						search: search
					});
				} else {
					AxiosFunc(`getBySearch`, {
						search: search
					});
				}
				return undefined;
			}
			if (location) {
				const loc = options.find((any) => any.url === location);
				if (!loc) {
					props.history.push('/');
					return undefined;
				} else {
					if (!props.appLocation) {
						const loc = options.find((any) => any.url === location);
						props.addLocation(loc.value);
					}
					if (url.search) {
						AxiosFunc(`getByLocation${url.search}`, {
							location: location
						});
					} else {
						AxiosFunc(`getByLocation`, {
							location: location
						});
					}
				}
				return console.log('loc');
			}
			if (subcategory) {
				const subcat = subCats.find((any) => any.url === subcategory);
				if (!subcat) {
					props.history.push('/');
					return undefined;
				}
				if (!props.subcategory) {
					console.log('added sub');
					props.addSubcategory(subcategory);
				}
				if (url.search) {
					AxiosFunc(`getBySubcategory${url.search}`, { subcategory: subcategory });
				} else {
					AxiosFunc(`getBySubcategory`, { subcategory: subcategory });
				}
				return console.log(' sub ');
			}
			if (!subcategory && !location && !search) {
				props.addSubcategory('');
				props.addLocation('');
				if (url.search) {
					AxiosFunc(`getAll${url.search}`, '');
				} else {
					AxiosFunc(`getAll`, '');
				}
				return undefined;
			}
		},
		[ urlChanged ]
	);
	return (
		<React.Fragment>
			<Spin size={'large'} spinning={props.spin}>
				<div className={`container ${classes.container}`}>
					<InfiniteScroll
						scrollThreshold={value}
						className="row m-1"
						hasMore={props.fetchedData.length < props.len}
						next={fetchOnScroll}
						dataLength={props.fetchedData.length}
					>
						{props.fetchedData.length ? (
							props.fetchedData.map((each) => {
								return (
									<Card
										onClick={() => props.history.push(`/add/${each._id}`)}
										key={each._id}
										className="col-lg-4 col-md-6 col-sm-12 col-12"
										style={{ width: 300 }}
									>
										<AddDetail
											imgUrl={`https://ravid-app.herokuapp.com/${each.img}`}
											classes={classes}
											each={each}
										/>
									</Card>
								);
							})
						) : (
							<P>آگهی یافت نشد</P>
						)}
					</InfiniteScroll>
				</div>
			</Spin>
		</React.Fragment>
	);
}

const mapState = (state) => {
	return {
		appLocation: state.appLocation,
		subcategory: state.subcategory,
		fetchedData: state.fetchedData,
		url: state.url,
		spin: state.spin,
		start: state.start,
		end: state.end,
		len: state.len,
		axiosUrl: state.axiosUrl,
		axiosObj: state.axiosObj
	};
};
const mapDis = (dispatch) => {
	return {
		addLocation: (location) => dispatch(addLocation(location)),
		addSubcategory: (subcategory) => dispatch(addSubcategory(subcategory)),
		fetchData: (data) => dispatch(fetchData(data)),
		setUrl: (url) => dispatch(setUrl(url)),
		setStart: () => dispatch(setStart()),
		setEnd: () => dispatch(setEnd()),
		setSpin: (spin) => dispatch(setSpin(spin)),
		setStartEnd: () => dispatch(setStartEnd()),
		setLen: (len) => dispatch(setLen(len))
	};
};

export default connect(mapState, mapDis)(withRouter(Adds));
