import { setEnd, setLen, setSpin, setStart, fetchData, setAxiosObj, setAxiosUrl } from '../../../redux/action';
import store from '../../../redux/store';
const axios = require('axios');
const serverUrl = `https://ravid-app.herokuapp.com/`;

export const AxiosFunc = (url, obj) => {
	const state = store.getState();
	store.dispatch(setSpin(true));
	store.dispatch(setAxiosUrl(url));
	store.dispatch(setAxiosObj(obj));
	axios
		.post(`${serverUrl}${url}`, obj)
		.then((res) => {
			store.dispatch(setLen(res.data.length));
			const sliced = res.data.slice(state.start, state.end);
			store.dispatch(fetchData(sliced));
			store.dispatch(setStart());
			store.dispatch(setEnd());
			store.dispatch(setSpin(false));
		})
		.catch((e) => {
			console.log(e.message + 'axios err');
		});
};
