import React from 'react';
import * as moment from 'jalali-moment';

function AddDetail(props) {
	const e2p = (s) => s.replace(/\d/g, (d) => '۰۱۲۳۴۵۶۷۸۹'[d]);

	const handleImgErr = (e) => {
		e.target.src = require('../../../app-data/img/default.png');
	};
	return (
		<div className="d-flex justify-content-between">
			<div className={props.classes.img}>
				<img onError={handleImgErr} alt="ad-img" className="img-fluid rounded" src={props.imgUrl} />
			</div>
			<div style={{ textAlign: 'right' }}>
				<h5>{props.each.title}</h5>
				<p
					style={{ direction: 'rtl' }}
					className={`${props.classes.ad__details} text-primary mb-1 font-weight-bold`}
				>
					{props.each.price.toLocaleString('ar-EG')}
					<p className="d-inline"> تومان</p>
				</p>
				<p className="text-muted mb-0">
					{e2p(`${moment(props.each.date).locale('fa').format('YY/M/D').toLocaleString('ar-EG')}`)}
				</p>
			</div>
		</div>
	);
}

export default AddDetail;
